ipuram is a small town in Kanchipuram district of TamilnaduThis place has a great history for its traditional historical mon- uments. Stone carving in Mahabalipuram is famous for its 7th and 8th century art and architecture of Pallavas period."),_c('br'),_c('br'),_vm._v(" Tamilnadu “The land of Temples” is rich in stone art. Mahabalipuram located 60 Km away from Chennai, is the main center for ancient stone carving. Red and black, white granites are available locally in Mahabalipuram. These stones are locally called as karuppu kal (black), Vellali kal (white). Granite is first stenciled and chiseled according to the determined lines. Af- ter carving the final product, the surface is smoothened and cleaned to achieve the shine luster. The products are the replicas of Hindu mythological epics. The rules of Shilpa Shastra are followed to carve granite stones. There are over 500 craftsmen involved in the carving."),_c('br'),_c('br'),_vm._v(" The stone carving is also practiced in other parts of Tamilnadu like Mylaudy, Suchindram from Kanyakumari dis- trict and in Madurai district."),_c('br'),_c('br'),_vm._v(" This mural celebrates the stone carving art and depicts the stories and architectural features of many temples . It has echos of the walls of temples which depict many episode from Hindu mythological epic carved which are also juxtaposed with architectural features from temples including figures of deities and rathas."),_c('br'),_c('br'),_vm._v(" The world famous Arjuna’s Penance which is an episode from Hindu epic Mahabharata is excellently composed, along with elements of skilled carvings involving complicated decorative architectural forms."),_c('br'),_c('br'),_vm._v(" This mural celebrates all these carvings and monuments that are the outstanding examples of Pallavas art and architecture and remarkable in Indian cultural heritage."),_c('br'),_c('br')])])],1)])],1)],1)],1)],1)],1)],1)],1)
